/* Helpers
================================================== */
  // Reset
  *,
  *:before,
  *:after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
  }
  audio,
  canvas,
  video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
  }
  audio:not([controls]) {
    display: none;
    height: 0;
  }
  [hidden] {
    display: none;
  }
  html {
    width: 100% !important;
    min-height: 100%;
    font-size: 100%;
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
  }
  html,
  button,
  input,
  select,
  textarea {
    font-family: sans-serif;
  }
  body {
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: none;
    line-height: 1.6em;
    margin: 0;
    padding: 0;
    height: 100%;
    overflow: auto;
  }
  table {
    margin: 0;
    font-family: $arial;
    box-sizing: border-box;
    font-size: 14px;
    border-collapse: collapse;
    border-spacing: 0;
    &.table-striped {
      tbody>tr:nth-of-type(even) {
        background-color: $white;
      }
      tbody>tr:nth-of-type(odd) {
        background-color: darken($off-white, 0);
      }
    }
    tbody {
      tr {
        height: 40px;
      }
      td {
        vertical-align: top;
      }
    }
  }

  img {
    max-width: 100%;
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
  }
  ul {
    list-style: none;
  }
  .relative {
    position: relative;
  }
  ::-webkit-input-placeholder {
    color: lighten($dim-grey, 10);
  }
